export default {
	data() {
		return {
			searchValue:'',
			auWidth:'86%'
		}
	},
	props:{
		//placeholder文字定义
		placeholder:{
			default:'可搜索律师/案情'
		},
		//是否默认聚焦
		autoFocus:{
			default:false
		},
		// 是否显示清除按钮
		isclearShow:{
			default:false
		},
		// 是否显示取消按钮
		isbackshow:{
			default:false
		},
		// 是否禁用输入框
		isdisabled:{
			default:false
		},
		// 键盘右下角文字
		confirmText:{
			default:'搜索'
		}
	},
	methods: {
		// input点击事件
		inputChange(){
			this.$emit('inputChange',this.searchValue)
		},
		inputClick(){
			this.$emit('inputClick',this.searchValue)
		},
		// 清空事件
		clearVlaue(){
			this.searchValue=''
		},
		back(){
			uni.navigateBack()
		}
	},
	mounted(){
		if(this.isbackshow){
			this.auWidth='86%'
		}else{
			this.auWidth="100%"
		}
	}
}
